import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import PropTypes from 'prop-types'
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { withTranslation } from "react-i18next"
import LeadBySource from "./lead"
import Apaexlinecolumn from "./apaexlinecolumn"
import DashedLine from "../AllCharts/apex/dashedLine"
import Revnue from "../AllCharts/apex/revnueLine"
import { Table, Thead, Tbody, Tr, Th, Td } from "react-super-responsive-table"
import CalenderShort from "../Activities/calander-short"

import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Select
} from "reactstrap"

class DashboardNew extends Component {
  constructor(props) {
    super(props)
    this.state = {
      reports: [
        { title: "Total Leads", iconClass: "bx-copy-alt", description: "14" },
        {
          title: "Hot Leads",
          iconClass: "bx-archive-in",
          description: "6",
        },
        {
          title: "Need Follow-up",
          iconClass: "bx-purchase-tag-alt",
          description: "5",
        },
      ]
    }
  }
  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <MetaTags>
            <title>Lead Dashboard | Realty Space</title>
          </MetaTags>
          <Container fluid>
             <Breadcrumbs
              title={this.props.t("Lead")}
              breadcrumbItem={this.props.t("Dashboard")}
            />
            <Row>
            {this.state.reports.map((report, key) => (
            <Col md="4" key={"_col_" + key}>
                <Card className="mini-stats-wid">
                        <CardBody>
                          <div className="d-flex">
                            <div className="flex-grow-1">
                              <p className="text-muted fw-medium">
                                {report.title}
                              </p>
                              <h4 className="mb-0">{report.description}</h4>
                            </div>
                            <div className="mini-stat-icon avatar-sm rounded-circle bg-primary align-self-center">
                              <span className="avatar-title">
                                <i
                                  className={
                                    "bx " + report.iconClass + " font-size-24"
                                  }
                                />
                              </span>
                            </div>
                          </div>
                        </CardBody>
                      </Card>
                
              </Col>
            ))}
            </Row>
            <Row>
              <Col md="7">
                  <Card>
                  <CardBody>
                  <div className="mb-4 card-title float-start">Calender</div>
                  <div className="clearfix"></div>
                  <CalenderShort />
                  </CardBody>
                  </Card>                  

              </Col>
              <Col md="5">
                  <Card>
                  <CardBody>
                  <div className="mb-4 card-title float-start">Call List</div>
                  <div className="clearfix"></div>
    
                  <div className="table-rep-plugin">
                                                <div
                                                    className="table-responsive mb-0"
                                                    data-pattern="priority-columns"
                                                >
                                                    <Table
                                                    id="tech-companies-1"
                                                    className="table table-striped table-bordered"
                                                    >
                                                    <Thead>
                                                        <Tr>
                                                       
                                                        <Th data-priority="1">{this.props.t("Name")}</Th>
                                                        <Th data-priority="3">{this.props.t("Phone")}</Th>
                                                       
                                                        <Th data-priority="3">{this.props.t("Detail")}</Th>
                                                    
                                                        </Tr>
                                                    </Thead>
                                                    <Tbody>
                                                        <Tr>
                                                        <Th>
                                                            
                                                            <span className="co-name">Mohit Grover</span>
                                                        </Th>
                                                        <Td> +1 123-321-4444  </Td>
                                                        <Td> <Link to="/lead/lead-detail" className="btn btn-primary">Detail</Link> </Td>
                                                        
                                                            
                                                            

                                                        </Tr>
                                                       
                                                        
                                                    </Tbody>
                                                    </Table>
                                                </div>
                                                </div>
                  </CardBody>
                  </Card>                  

              </Col>
             
            </Row>
            <Row>
              <Col lg="6">
                <Card>
                <CardBody>
                <div className="mb-4 card-title float-start">Lead By Source</div>
                <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                <LeadBySource />
                </CardBody>
                </Card>
              </Col>
              <Col lg="6">
                <Card>
                <CardBody>
                <div className="mb-4 card-title float-start">Lead By Source</div>
                <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                <Apaexlinecolumn />
               
                </CardBody>
                </Card>
              </Col>
            </Row>
            <Row>
              <Col lg="6">
                <Card>
                <CardBody>
                <div className="mb-4 card-title float-start">Lead By Source</div>
                <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                <DashedLine />
                </CardBody>
                </Card>
              </Col>
              <Col lg="6">
                <Card>
                <CardBody>
                <div className="mb-4 card-title float-start">Lead By Source</div>
                <div className="float-end">
                       <select name="" id="" className="form-control">
                         <option value="">Select Days</option>
                         <option value="">30 Days</option>
                         <option value="">60 Days</option>
                         <option value="">90 Days</option>
                         <option value="">120 Days</option>
                      </select>             

                </div>
                <div className="clearfix"></div>
                <Revnue />
               
                </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
DashboardNew.propTypes = {
  t: PropTypes.any

}
export default connect()(withTranslation()(DashboardNew))
